import React, { useState, useEffect } from 'react';
import { urlFor, client } from '../../client';

const Copy = () => {
  // # Logo Image
  const [images, setImage] = useState([]);
  useEffect(() => {
    const query = '*[_type == "assets"]';
    client.fetch(query).then((data) => {
      setImage(data);
    });
  }, []);
  // # Website Keywords
  const [keyword, setKeyword] = useState([]);
  useEffect(() => {
    const query = '*[_type == "keywords"]';
    client.fetch(query).then((data) => {
      setKeyword(data);
    });
  }, []);
  return (
    <div className="copyright footer-logo">
      {images.map((data) => (
        <a href="#home">
          <img src={urlFor(data.logo)} alt="logo" />
        </a>
      ))}
      {keyword.map((word) => (
        <p className="p"> {word.copyright[0]} &copy; {word.copyright[1]} <span> {word.copyright[2]} </span> </p>
      ))}
    </div>
  );
};

export default Copy;
