import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import EduData from './EduData';
import { AppWrap, MotionWrap } from '../../wrapper';
import { client } from '../../client';
import './Education.scss';

const Education = () => {
  const [educations, setEducation] = useState([]);
  useEffect(() => {
    const query = '*[_type == "educations"]';
    client.fetch(query).then((data) => {
      setEducation(data);
    });
  }, []);

  return (
    <>
      <h2 className="head-text">My<span> Education </span></h2>
      {educations.map((education) => (
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1, transition: { duration: 0.5 } }}
          className="lg:w-[60%]  sm:w-[95%] lg:ml-20 lg:mt-3 flex flex-col lgl:flex-row gap-10 lgl:gap-20"
        >
          <div>
            <div style={{ marginBottom: 70 }} className="mt-10 lgl:mt-14 w-full lg:h-[700px] sm:h-[600px]  lg:border-l-[6px] lg:border-l-black lg:border-opacity-30 flex flex-col gap-10">
              {education.education1.map((data) => (
                <EduData
                  year={data.year}
                  college={data.title}
                  tag={data.tag}
                  result={data.result}
                  board={data.board}
                  desc={data.headline}
                  rank={data.rank}
                  location={data.location}
                />
              ))}
              {education.education2.map((data) => (
                <EduData
                  year={data.year}
                  college={data.title}
                  tag={data.tag}
                  result={data.result}
                  board={data.board}
                  desc={data.headline}
                  rank={data.rank}
                  location={data.location}
                />
              ))}
              {education.education3.map((data) => (
                <EduData
                  year={data.year}
                  college={data.title}
                  tag={data.tag}
                  result={data.result}
                  board={data.board}
                  desc={data.headline}
                  rank={data.rank}
                  location={data.location}
                />
              ))}
            </div>
          </div>
        </motion.div>
      ))}
    </>
  );
};

export default AppWrap(
  MotionWrap(Education, 'app__skills'),
  'education',
  'app__whitebg',
);
