import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import ReactTooltip from 'react-tooltip';
import { FooterWrap, MotionWrap } from '../../wrapper';
import { urlFor, client } from '../../client';
import './Footer.scss';

const Footer = () => {
  const [formData, setFormData] = useState({ name: '', email: '', message: '' });
  const [isFormSubmitted, setIsFormSubmitted] = useState(false);
  const [loading, setLoading] = useState(false);
  const [msg, setMessage] = useState('null');
  let i;
  const { username, email, message } = formData;
  const handleChangeInput = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  // # Contact Images
  const [images, setImage] = useState([]);
  useEffect(() => {
    const query = '*[_type == "assets"]';
    client.fetch(query).then((data) => {
      setImage(data);
    });
  }, []);

  // # Contact Details
  const [Data, setData] = useState([]);
  useEffect(() => {
    const query = '*[_type == "mycontact"]';
    client.fetch(query).then((data) => {
      setData(data);
    });
  }, []);

  const handleSubmit = () => {
    i = 0;
    setMessage('null');
    setLoading(true);
    if (formData.username === '') {
      setMessage('Username Is Required ');
      i += 1;
    }
    if (formData.email === '') {
      setMessage('Please Give Your Email ');
      i += 1;
    }
    if (formData.message === '') {
      setMessage('Please Write Some Message ');
      i += 1;
    }
    if (i === 2) {
      i = 0;
      setMessage('Please Fill Up All The Details ');
    }
    if (i === 3) {
      i = 0;
      setMessage('Please Fill Up The Form First ');
    }
    if (msg === 'null' && i === 0) {
      const contact = {
        _type: 'feedback1',
        name: formData.username,
        email: formData.email,
        message: formData.message,
      };
      client.create(contact)
        .then(() => {
          setLoading(false);
          setIsFormSubmitted(true);
          setTimeout(
            () => setIsFormSubmitted(false),
            3000,
          );
        })
        .catch((err) => console.log(err));
    } else {
      setLoading(false);
      setIsFormSubmitted(true);
      setTimeout(
        () => setIsFormSubmitted(false),
        3000,
      );
    }
  };
  return (
    <>
      <h2 className="head-text">Get In <span> Touch </span></h2>
      {images.map((data) => (

        Data.map((contact) => (
          <div className="app__footer-cards">
            <motion.div
              data-tip
              data-for={contact.email}
              key={contact.email}
            >
              <div className="app__footer-card">
                <img src={urlFor(data.email)} alt="email" />
                <a href={contact.link1} className="p-text">{contact.email}</a>
              </div>
            </motion.div>
            <motion.div
              data-tip
              data-for={contact.mobile}
              key={contact.mobile}
            >
              <div className="app__footer-card">
                <img src={urlFor(data.mobile)} alt="mobile" />
                <a href={contact.link2} className="p-text"> {contact.mobile} </a>
              </div>
            </motion.div>
            <motion.div
              data-tip
              data-for={contact.address}
              key={contact.address}
            >
              <div className="app__footer-card">
                <img src={urlFor(data.map)} alt="mao" />
                <a href={contact.link3} className="p-text"> {contact.address} </a>
              </div>
            </motion.div>
            <ReactTooltip
              id={contact.address}
              effect="solid"
              arrowColor="#fff"
              className="skills-tooltip"
            >
              {contact.address1}
            </ReactTooltip>
            <ReactTooltip
              id={contact.email}
              effect="solid"
              arrowColor="#fff"
              className="skills-tooltip"
            >
              {contact.email}
            </ReactTooltip>
            <ReactTooltip
              id={contact.mobile}
              effect="solid"
              arrowColor="#fff"
              className="skills-tooltip"
            >
              {contact.mobile}
            </ReactTooltip>
          </div>
        ))
      ))}
      {!isFormSubmitted ? (
        <div className="app__footer-form app__flex">
          <div className="app__flex">
            <input className="p-text" type="text" placeholder="Your Name" name="username" value={username} onChange={handleChangeInput} />
          </div>
          <div className="app__flex">
            <input className="p-text" type="email" placeholder="Your Email" name="email" value={email} onChange={handleChangeInput} />
          </div>
          <div>
            <textarea
              className="p-text"
              placeholder="Your Message"
              value={message}
              name="message"
              onChange={handleChangeInput}
            />
          </div>
          {images.map((data) => (
            <button type="button" className="p-text flex flex-row  hover:bg-green-500 font-bold" onClick={handleSubmit}>{!loading ? 'Send Message' : 'Sending...'}
              {!loading ? (<img className="flex flex-row" src={urlFor(data.send)} alt="send" />) : ('')}
            </button>
          ))}
        </div>
      ) : (
        <div>
          {msg === 'null' ? (
            <h3 className="msg">
               Thank you for getting in touch with <span> Suraj </span>  
            </h3>
          ) : (
            <h3 className="errmsg">
              {msg}
            </h3>
          )}
        </div>
      )}
    </>
  );
};

export default FooterWrap(
  MotionWrap(Footer, 'app__footer'),
  'contact',
  'app__whitebg',
);
