import React, { useEffect, useState } from 'react';
import { HiMenuAlt4, HiX } from 'react-icons/hi';
import { motion } from 'framer-motion';
import { urlFor, client } from '../../client';
import './Navbar.scss';

const Navbar = () => {
  const [toggle, setToggle] = useState(false);

  // # Atom Logo Image
  const [images, setImage] = useState([]);
  useEffect(() => {
    const query = '*[_type == "assets"]';
    client.fetch(query).then((data) => {
      setImage(data);
    });
  }, []);

  // # Website Keywords
  const [keyword, setKeyword] = useState([]);
  useEffect(() => {
    const query = '*[_type == "keywords"]';
    client.fetch(query).then((data) => {
      setKeyword(data);
    });
  }, []);
  return (
    <nav className="app__navbar">
      {images.map((data) => (
        <div className="app__navbar-logo sm:mr-5">
          {/* <img src={images.logo} alt="logo" /> */}
          <a href="#home" className="fas fa-spin">
            <img src={urlFor(data.atom)} alt="atom" />
          </a>
          {keyword.map((word) => (
            <h2 className="author-text"><span>{word.website[0]}</span>{word.website[1]} </h2>
          ))}
        </div>
      ))}
      <ul className="app__navbar-links">
        {/* {['home', 'about', 'skills', 'projects', 'education', 'contact'].map((item) => ( */}
        {keyword.map((word) => (
          word.header.map((item) => (
            <li className="app__flex  p-text" key={`link-${item}`}>
              <div />
              <a href={`#${item}`}>{item}</a>
              {/* <span className="block h-0.8 w-full bg-transparent hover:bg-purple-600"> </span> */}
            </li>
          ))
        ))}
      </ul>

      <div className="app__navbar-menu">
        <HiMenuAlt4 onClick={() => setToggle(true)} />

        {toggle && (
          <motion.div
            whileInView={{ x: [300, 0] }}
            transition={{ duration: 0.7, ease: 'easeOut' }}
          >
            <HiX onClick={() => setTimeout(() => {
              setToggle(false);
            }, 200)}
            />
            <ul>
              {keyword.map((word) => (
                word.header.map((item) => (
                  <li key={item}>
                    <a href={`#${item}`} onClick={() => setToggle(false)}>
                      {item}
                    </a>
                  </li>
                ))
              ))}
            </ul>
          </motion.div>
        )}
      </div>
    </nav>
  );
};

export default Navbar;
