import React, { useEffect, useState } from 'react';
import { motion } from 'framer-motion';
import { urlFor, client } from '../../client';
import { AppWrap, MotionWrap } from '../../wrapper';
import './Skills.scss';

const Skills = () => {
  const [works, setWorks] = useState([]);
  const [filterWork, setFilterWork] = useState([]);
  const [filterWork1, setFilterWork1] = useState([]);
  const [program, setProgram] = useState([]);
  const [activeFilter, setActiveFilter] = useState('Programming');
  const [activeFilter1, setActiveFilter1] = useState('All');
  // const [skillFilter, setSkillFilter] = useState('All');
  useEffect(() => {
    const query = '*[_type == "features"]';
    client.fetch(query).then((data) => {
      setWorks(data);
    });
  }, []);

  useEffect(() => {
    const query = '*[_type == "program"]';
    client.fetch(query).then((data) => {
      setProgram(data);
    });
  }, []);

  const handleWorkFilter = (item) => {
    setActiveFilter(item);
    setTimeout(() => {
      setFilterWork(works.filter((work) => work.tag.includes(item)));
    }, 500);
  };

  const [skills, setSkills] = useState([]);
  useEffect(() => {
    const skillsQuery = '*[_type == "skills"]';
    client.fetch(skillsQuery).then((data) => {
      setSkills(data);
    });
  }, []);

  const [skills2, setSkills2] = useState([]);
  useEffect(() => {
    const skillsQuery = '*[_type == "skills2"]';
    client.fetch(skillsQuery).then((data) => {
      setSkills2(data);
    });
  }, []);

  const handleWorkFilter1 = (item) => {
    setActiveFilter1(item);
    setTimeout(() => {
      setFilterWork1(skills2.filter((skill) => skill.tag.includes(item)));
    }, 500);
  };

  // const handleSkillFilter = (item) => {
  //   setSkillFilter(item);
  //   setTimeout(() => {
  //     setFilterWork(works.filter((work) => work.tag.includes(item)));
  //   }, 500);
  // };

  // # Website Keywords
  const [keyword, setKeyword] = useState([]);
  useEffect(() => {
    const query = '*[_type == "keywords"]';
    client.fetch(query).then((data) => {
      setKeyword(data);
    });
  }, []);

  return (
    <>
      <h2 className="head-text">Technical <span> Skills </span></h2>
      {keyword.map((word) => (
        <div className="app__work-filter">
          {/* {['Other', 'Tools', 'CSS', 'Frontend', 'Backend', 'Development', 'Programming'].map((item, index) => ( */}
          {word.skill.map((item, index) => (
            <div
              key={index}
              onClick={() => handleWorkFilter(item)}
              className={`app__work-filter-item app__flex hidden md:block p-text ${activeFilter === item ? 'item-active' : ''}`}
            >
              {item}
            </div>
          ))}
        </div>
      ))}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1, transition: { duration: 0.5 } }}
        className="w-full flex flex-col lgl:flex-row gap-10 lgl:gap-20"
      >
        <div className="container w-full lgl:w-1/2">
          {keyword.map((word) => (
            <div className="app__work-filter2 lg:items-center lg:justify-center lg:ml-15 lg:mt-16 sm:ml-10 ">
              {word.images.map((item, index) => (
                <div
                  key={index}
                  onClick={() => handleWorkFilter1(item)}
                  className={`app__work-filter-item2  app__flex p-text ${activeFilter1 === item ? 'item-active' : ''}`}
                >
                  {item}
                </div>
              ))}
            </div>
          ))}
          <div className="app__skills-container lg:ml-20 ">
            {activeFilter1 !== 'All' ? (
              <motion.div className="app__skills-list">
                {filterWork1.map((skill) => (
                  <motion.div
                    whileInView={{ opacity: [0, 1] }}
                    transition={{ duration: 0.5 }}
                    className="app__skills-item app__flex"
                    key={skill.name}
                  >
                    <div
                      className="app__flex"
                      style={{ backgroundColor: skill.bgColor }}
                    >
                      <img src={urlFor(skill.icon)} alt={skill.name} />
                    </div>
                    <p className="p-text">{skill.name}</p>
                  </motion.div>
                ))}
              </motion.div>
            ) : (
              <motion.div className="app__skills-list">
                {skills.map((skill) => (
                  <motion.div
                    whileInView={{ opacity: [0, 1] }}
                    transition={{ duration: 0.5 }}
                    className="app__skills-item app__flex"
                    key={skill.name}
                  >
                    <div
                      className="app__flex"
                      style={{ backgroundColor: skill.bgColor }}
                    >
                      <img src={urlFor(skill.icon)} alt={skill.name} />
                    </div>
                    <p className="p-text">{skill.name}</p>
                  </motion.div>
                ))}
              </motion.div>
            )}
          </div>
        </div>
        {keyword.map((word) => (
          <div className="app__work-filter1 sm:mt-5  sm:ml-5">
            {/* {['Other', 'Tools', 'CSS', 'Frontend', 'Backend', 'Development', 'Programming'].map((item, index) => ( */}
            {word.skill.map((item, index) => (
              <div
                key={index}
                onClick={() => handleWorkFilter(item)}
                className={`app__work-filter-item1 app__flex lg:hidden p-text ${activeFilter === item ? 'item-active' : ''}`}
              >
                {item}
              </div>
            ))}
          </div>
        ))}
        {activeFilter !== 'Programming' ? (
          filterWork.map((work) => (
            <div className="w-full sm:pl-8 lg:mt-5 lg:pl-20 lg:mx-5 lgl:w-1/2">
              <div className="lg:py-12 sm:py-5 font-titleFont flex flex-col gap-4">
                <p className="text-sm font-medium text-designColor tracking-[4px] uppercase">
                  Features
                </p>
                <h2 style={{ marginRight: work.margin }} className="head text-2xl flex flex-row md:text-4xl font-bold">  {work.title1} <span className="ml-2">  {work.title2} </span></h2>
              </div>
              <div className='className="mt-14 w-full flex flex-col gap-6'>
                <div className="overflow-x-hidden">
                  <p className="skiller"> {work.feature1} </p>
                  <span className="lg:w-[70%] sm:w-[90%] h-2.5 bg-gray-300  rounded-md inline-flex mt-2">
                    <motion.div
                      initial={{ x: '-100%', opacity: 0 }}
                      animate={{ x: 0, opacity: 1 }}
                      transition={{ duration: 0.5, delay: 0.5 }}
                      className={work.class1}
                    >
                      <span className="absolute percent -top-7 right-0">{work.percent1}</span>
                    </motion.div>
                  </span>
                </div>
                <div className="overflow-x-hidden">
                  <p className="skiller">{work.feature2}</p>
                  <span className="lg:w-[70%] sm:w-[90%] h-2.5 bg-gray-300 rounded-md inline-flex mt-2">
                    <motion.div
                      initial={{ x: '-100%', opacity: 0 }}
                      animate={{ x: 0, opacity: 1 }}
                      transition={{ duration: 0.5, delay: 0.5 }}
                      className={work.class2}
                    >
                      <span className="absolute percent -top-7 right-0">{work.percent2}</span>
                    </motion.div>
                  </span>
                </div>
                <div className="overflow-x-hidden">
                  <p className="skiller">{work.feature3}</p>
                  <span className="lg:w-[70%] sm:w-[90%] h-2.5 bg-gray-300  rounded-md inline-flex mt-2">
                    <motion.div
                      initial={{ x: '-100%', opacity: 0 }}
                      animate={{ x: 0, opacity: 1 }}
                      transition={{ duration: 0.5, delay: 0.5 }}
                      className={work.class3}
                    >
                      <span className="absolute percent -top-7 right-0">{work.percent3}</span>
                    </motion.div>
                  </span>
                </div>
              </div>
            </div>
          ))
        ) : (
          program.map((work) => (
            <div className="w-full sm:pl-8 lg:mt-5  lg:mx-5 lg:pl-20 lgl:w-1/2">
              <div className="lg:py-12 sm:py-5 font-titleFont flex flex-col gap-4">
                <p className="text-sm v text-designColor tracking-[4px] font-medium uppercase">
                  Features
                </p>
                <h2 style={{ marginRight: work.margin }} className="head text-2xl flex flex-row md:text-4xl font-bold">  {work.title1} <span className="ml-2">  {work.title2} </span></h2>
              </div>
              <div className='className="mt-14 w-full flex flex-col gap-6'>
                <div className="overflow-x-hidden">
                  <p className="skiller"> {work.feature1} </p>
                  <span className="lg:w-[70%] sm:w-[90%]  h-2.5 bg-gray-300  rounded-md inline-flex mt-2">
                    <motion.div
                      initial={{ x: '-100%', opacity: 0 }}
                      animate={{ x: 0, opacity: 1 }}
                      transition={{ duration: 0.5, delay: 0.5 }}
                      className={work.class1}
                    >
                      <span className="absolute percent  -top-7 right-0">{work.percent1}</span>
                    </motion.div>
                  </span>
                </div>
                <div className="overflow-x-hidden">
                  <p className="skiller">{work.feature2}</p>
                  <span className="lg:w-[70%] sm:w-[90%] h-2.5 bg-gray-300 rounded-md inline-flex mt-2">
                    <motion.div
                      initial={{ x: '-100%', opacity: 0 }}
                      animate={{ x: 0, opacity: 1 }}
                      transition={{ duration: 0.5, delay: 0.5 }}
                      className={work.class2}
                    >
                      <span className="absolute  percent -top-7 right-0">{work.percent2}</span>
                    </motion.div>
                  </span>
                </div>
                <div className="overflow-x-hidden">
                  <p className="skiller">{work.feature3}</p>
                  <span className="lg:w-[70%] sm:w-[90%] h-2.5 bg-gray-300  rounded-md inline-flex mt-2">
                    <motion.div
                      initial={{ x: '-100%', opacity: 0 }}
                      animate={{ x: 0, opacity: 1 }}
                      transition={{ duration: 0.5, delay: 0.5 }}
                      className={work.class3}
                    >
                      <span className="absolute percent  -top-7 right-0">{work.percent3}</span>
                    </motion.div>
                  </span>
                </div>
              </div>
            </div>
          ))
        )}
      </motion.div>
    </>
  );
};

export default AppWrap(
  MotionWrap(Skills, 'app__feature'),
  'skills',
  'app__pinkbg',
);
