import React, { useEffect, useState } from 'react';
import { Cursor } from 'react-simple-typewriter';
import { motion } from 'framer-motion';
import ReactTooltip from 'react-tooltip';
// import Typewriter from 'typewriter-effect';
import { client } from '../../client';
import './Education.scss';

const EduData = ({ year, college, tag, result, board, desc, rank, location }) => {
  const [educations, setEducation] = useState([]);

  useEffect(() => {
    const query = '*[_type == "educations"]';
    client.fetch(query).then((data) => {
      setEducation(data);
    });
  }, []);
  return (
    <div className="lg:w-[95%]  sm:w-full lg:h-1/3 sm:h-1/2 group flex ">
      <div className="w-10 h-[4px] hidden md:block bgOpacity mt-16 relative">
        <span className="absolute w-5 h-5 rounded-full -top-2 -left-3 flex justify-center items-center bg-black bg-opacity-60">
          <span className="w-3 h-3 rounded-full bg-purple-600 inline-flex group-hover:bg-yellow-400 duration-300"> </span>
        </span>
      </div>
      <div className="w-full  hover:scale-105 shadow-2xl lg:bg-violet-600 sm:bg-purple-600 duration-300  rounded-lg p-4 lgl:px-10 flex flex-col justify-center gap-6 lgl:gap-10 lg:shadow-shadowOne sm:shadow-shadowOne1">
        <div className="flex flex-col lgl:flex-row justify-between gap-4 lgl:gap-0 lgl:items-center">
          <div>
            <motion.div
              data-tip
              data-for={year}
              key={year}
            >
              <p className="text-sm mt-2 font-semibold lg:text-gray-400 sm:text-gray-300 duration-300">
                {year}
              </p>
            </motion.div>
            {(tag === 'B.Tech' || tag === 'HSC') ? (
              <h3 className="text-xl sm:text-sm md:text-xl font-semibold group-hover:text-yellow-500 duration-300">
                {college} {location}
              </h3>
            ) : (
              <h3 className="text-xl flex sm:flex-row sm:text-sm md:text-xl font-semibold group-hover:text-yellow-500 duration-300">
                {college}  <br /> {location}
              </h3>
            )}
          </div>
          <div className="mx-3">
            <p className="px-4 hidden md:block mx-3 py-2 tag bg-black bg-opacity-25 rounded-lg group-hover:text-yellow-400 shadow-shadowOne text-sm font-bold">
              {tag}
            </p>
          </div>
          <ReactTooltip
            id={year}
            effect="solid"
            arrowColor="#fff"
            className="skills-tooltip"
          >
            {desc}
          </ReactTooltip>
        </div>
        {tag === 'B.Tech' ? (
          educations.map((education) => (
            <p className="text-sm md:text-base font-bold lg:text-gray-400  sm:text-gray-300 lg:group-hover:text-gray-300 duration-300">
              {education.Id} <br />
              {board}
              <span className="lg:ml-1 sm:ml-1 text-xl">
                <Cursor
                  cursorBlinking="false"
                  cursorStyle="|"
                  cursorColor="black"
                />
              </span>
              <span className="ml-1"> {result} </span>
            </p>
          ))
        ) : (
          <p className="text-sm md:text-base font-bold lg:text-gray-400 sm:text-gray-300 lg:group-hover:text-gray-300 duration-300">
            {rank} <br /> {board}
            <span className="lg:ml-1 sm:ml-1 text-xl">
              <Cursor
                cursorBlinking="false"
                cursorStyle="|"
                cursorColor="black"
              />
            </span>
            <span className="ml-1"> {result} </span>
          </p>
        )}
      </div>
    </div>
  );
};

export default EduData;
