import React, { useEffect, useState } from 'react';
import CountUp from 'react-countup';
import ScrollTrigger from 'react-scroll-trigger';
import { AppWrap1, MotionWrap } from '../../wrapper';
import { client } from '../../client';
import './Statistics.scss';
import images from '../../constants/images';

const Statistics = () => {
  const [stat, setStat] = useState([]);
  const [counterOn, setCounterOn] = useState(false);
  useEffect(() => {
    const query = '*[_type == "stats"]';
    client.fetch(query).then((data) => {
      setStat(data);
    });
  }, []);

  return (
    <>
      <h2 style={{ marginBottom: 50 }} className="head-text">Project <span> Statistics </span></h2>
      {stat.map((data) => (
        <div className="experience lg:mt-5 text" id="experience">
          <div className="flex lg:flex-row sm:flex-col">
            <div>
              <img src={images.projects} className="mx-auto mt-3 Images w-auto" alt="icon puzzle" />
            </div>
            <div className="achievement">
              <ScrollTrigger onEnter={() => setCounterOn(true)} onExit={() => setCounterOn(false)}>
                <div className="mt-5 circle ">
                  {counterOn && <CountUp start={0} end={data.projects} duration={4} delay={0} />}
                </div>
              </ScrollTrigger>
              <span className="text">Finished Projects </span>
            </div>
          </div>

          <div className="flex lg:flex-row sm:flex-col">
            <div>
              <img src={images.awards} className="sm:mx-auto lg:mx-5 Images mt-3  w-auto" alt="icon puzzle" />
            </div>
            <div className="achievement">
              <div className="mt-5 circle ">
                {counterOn && <CountUp start={0} end={data.awards} duration={4} delay={0} />}
              </div>
              <span className="text"> Awards Won </span>
            </div>
          </div>

          <div className="flex lg:flex-row sm:flex-col">
            <div>
              <img src={images.bugs} className="lg:mx-5 sm:mx-auto Images mt-3 w-auto" alt="icon puzzle" />
            </div>
            <div className="achievement">
              <div className="circle mt-5 ">
                {counterOn && <CountUp start={0} end={data.bugs} duration={4} delay={0} />}
                {data.plus}
              </div>
              <span className="text">Bugs Fixed </span>
            </div>
          </div>
        </div>
      ))}
    </>
  );
};

export default AppWrap1(
  MotionWrap(Statistics, 'app__experience'),
  'statistics',
  'app__pinkbg',
);
