import React, { useState, useEffect } from 'react';
import { AiFillEye, AiFillGithub } from 'react-icons/ai';
import { motion } from 'framer-motion';

import { AppWrap, MotionWrap } from '../../wrapper';
import { urlFor, client } from '../../client';
import './Project.scss';

const Project = () => {
  const [works, setWorks] = useState([]);
  const [tag, setTag] = useState(false);
  const [filterWork, setFilterWork] = useState([]);
  const [activeFilter, setActiveFilter] = useState('All');
  const [animateCard, setAnimateCard] = useState({ y: 0, opacity: 1 });
  useEffect(() => {
    const query = '*[_type == "works"]';

    client.fetch(query).then((data) => {
      setWorks(data);
      setFilterWork(data);
    });
  }, []);

  // # Absent Projects
  const [Data, setData] = useState([]);
  useEffect(() => {
    const query = '*[_type == "absent"]';
    client.fetch(query).then((data) => {
      setData(data);
    });
  }, []);

  const handleWorkFilter = (item, p1, p2) => {
    setTag(false);
    if (item === p1 || item === p2) {
      setTag(true);
    }
    setActiveFilter(item);
    setAnimateCard([{ y: 100, opacity: 0 }]);

    setTimeout(() => {
      setAnimateCard([{ y: 0, opacity: 1 }]);
      if (item === 'All') {
        setFilterWork(works);
      } else {
        setFilterWork(works.filter((work) => work.tags.includes(item)));
      }
    }, 500);
  };

  // # Website Keywords
  const [keyword, setKeyword] = useState([]);
  useEffect(() => {
    const query = '*[_type == "keywords"]';
    client.fetch(query).then((data) => {
      setKeyword(data);
    });
  }, []);
  return (
    <>
      <h2 className="head-text">My <span>Projects</span> </h2>
      {Data.map((suraj) => (
        keyword.map((word) => (
          <div className="app__work-filter">
            {/* {['Frontend', 'Backend', 'Angular JS', 'React JS', 'MEAN', 'MERN', 'Full Stack', 'All'].map((item, index) => ( */}
            {word.project.map((item, index) => (
              <div
                key={index}
                onClick={() => handleWorkFilter(item, suraj.p1, suraj.p2)}
                className={`app__work-filter-item app__flex p-text ${activeFilter === item ? 'item-active' : ''}`}
              >
                {item}
              </div>
            ))}
          </div>
        ))
      ))}

      {(!tag) ? (
        <motion.div
          animate={animateCard}
          transition={{ duration: 0.5, delayChildren: 0.5 }}
          className="app__work-portfolio"
        >
          {filterWork.map((work, index) => (
            <div className="app__work-item app__flex" key={index}>
              <div
                className="app__work-img app__flex"
              >
                <img src={urlFor(work.imgUrl)} alt={work.name} />

                <motion.div
                  whileHover={{ opacity: [0, 1] }}
                  transition={{ duration: 0.25, ease: 'easeInOut', staggerChildren: 0.5 }}
                  className="app__work-hover app__flex"
                >
                  <a href={work.projectLink} target="_blank" rel="noreferrer">

                    <motion.div
                      whileInView={{ scale: [0, 1] }}
                      whileHover={{ scale: [1, 0.90] }}
                      transition={{ duration: 0.25 }}
                      className="app__flex"
                    >
                      <AiFillEye />
                    </motion.div>
                  </a>
                  <a href={work.codeLink} target="_blank" rel="noreferrer">
                    <motion.div
                      whileInView={{ scale: [0, 1] }}
                      whileHover={{ scale: [1, 0.90] }}
                      transition={{ duration: 0.25 }}
                      className="app__flex"
                    >
                      <AiFillGithub />
                    </motion.div>
                  </a>
                </motion.div>
              </div>
              <div className="app__work-content app__flex">
                <h4 className="bold-text"> {work.title} </h4>
                <h6 className="text"><span> {work.lang} </span> </h6>
                <p className="p-text" style={{ marginTop: 10 }}>{work.description}</p>
                <h5>{work.date}</h5>
                <div className="app__work-tag  app__flex">
                  <p className="p-text">{work.topic[0]}</p>
                </div>
              </div>
              <div className="lg:hidden border-t-[2px] mt-2 w-full border-t-black border-opacity-30 ">
                <motion.div
                  whileHover={{ opacity: [0, 1] }}
                  transition={{ duration: 0.25, ease: 'easeInOut', staggerChildren: 0.5 }}
                  className="lg:hidden code gap-10 app__flex"
                >
                  <a href={work.projectLink} target="_blank" rel="noreferrer">

                    <motion.div
                      whileInView={{ scale: [0, 1] }}
                      whileHover={{ scale: [1, 0.90] }}
                      transition={{ duration: 0.25 }}
                      className="app__flex"
                    >
                      <AiFillEye />
                    </motion.div>
                  </a>
                  <a href={work.codeLink} target="_blank" rel="noreferrer">
                    <motion.div
                      whileInView={{ scale: [0, 1] }}
                      whileHover={{ scale: [1, 0.90] }}
                      transition={{ duration: 0.25 }}
                      className="app__flex"
                    >
                      <AiFillGithub />
                    </motion.div>
                  </a>
                </motion.div>
              </div>
            </div>
          ))}
        </motion.div>
      ) : (
        <motion.div
          animate={animateCard}
          transition={{ duration: 0.8, delayChildren: 0.9 }}
          className="app__work-portfolio"
        >
          <div className="sm:mt-20">
            <h2 className="head">Coming <span>Soon..</span>. </h2>
          </div>
        </motion.div>
      )}
    </>
  );
};

export default AppWrap(
  MotionWrap(Project, 'app__works'),
  'projects',
  'app__primarybg',
);
